import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FlexLayoutModule } from '@angular/flex-layout';
import { DemoMaterialModule } from '../../demo-material-module';
import { PipeModule } from '../Pipes/pipe/pipe.module';
import { CreateComponent } from './create/create.component';
import { UpcomingComponent } from './upcoming/upcoming.component';
import { AucationRoutes } from './aucation.routing';
import { AucationhistoryComponent } from './aucationhistory/aucationhistory.component';
import { ParticipantaucationComponent } from './participantaucation/participantaucation.component';
import { AucationarchiveComponent } from './aucationarchive/aucationarchive.component';

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(AucationRoutes),
    DemoMaterialModule,
    FlexLayoutModule,
    FormsModule,
    ReactiveFormsModule,
    PipeModule
  ],
  declarations: [
    CreateComponent,
    UpcomingComponent,
    AucationhistoryComponent,
    ParticipantaucationComponent,
    AucationarchiveComponent,
  ]
})
export class AucationModule { }
